import React from 'react';
import PropTypes from 'prop-types';
import { Root, View, Panel , FormLayout, Input, Select, Checkbox, Button, PanelHeader, Group, Header, Cell} from '@vkontakte/vkui';

const Home = ({ id, go }) => (
	<Panel id={id}>
		<PanelHeader>Расчет стоимости пошива</PanelHeader>
		<FormLayout>
            <Group  header={<Header mode="secondary">Размер одной шторы</Header>}>
	            	<Cell>
	            		<div style={{ display: 'flex' }}>
	            			<Input placeholder="Ширина(см)" />
	            			<Input placeholder="Высота(см)" />
	            		</div>
	            	</Cell>
            </Group>
            <Input top="Количество штор" defaultValue="2"/>

            <Select top="Ширина ленты" placeholder="Выберите ленту" defaultValue="4 см">
              <option value="6">6 см</option>
              <option value="4">4 см</option>
            </Select>

            <Checkbox>Подшивать низ</Checkbox>
            <Button size="xl" onClick={go} data-to="persik">Рассчитать</Button>
          </FormLayout>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Home;
